import { Tabs, Tab } from '@mui/material';

import Transactions from './Transactions';
import Categories from './Categories';
import Institutions from './Institutions';
import Accounts from './Accounts';
import Transformations from './transformations/Transformations';

export default function Home({ state, dispatch }) {
    return (
        <>
            <Tabs value={state['home']['tab']} onChange={(e, tab) => {
                localStorage.setItem('home-tab', tab);
                dispatch({ home: { ...state['home'], tab: tab } });
            }}>
                <Tab value='transactions' label='Transactions' />
                <Tab value='categories' label='Categories' />
                <Tab value='institutions' label='Institutions' />
                <Tab value='accounts' label='Accounts' />
                <Tab value='transformations' label='Transformations' />
            </Tabs>
            <Transactions state={state} dispatch={dispatch} hidden={state['home']['tab'] !== 'transactions'} />
            <Categories state={state} dispatch={dispatch} hidden={state['home']['tab'] !== 'categories'} />
            <Institutions state={state} dispatch={dispatch} hidden={state['home']['tab'] !== 'institutions'} />
            <Accounts state={state} dispatch={dispatch} s hidden={state['home']['tab'] !== 'accounts'} />
            <Transformations state={state} dispatch={dispatch} hidden={state['home']['tab'] !== 'transformations'} />
        </>
    );
}