import { Box, Button, TextField, Dialog, DialogTitle, DialogContent, DialogActions, Divider, Stack, InputLabel, FormControl, Select, MenuItem } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';

export default function Accounts({ state, dispatch, hidden }) {
    return (
        <Box sx={{ display: hidden ? 'none' : 'flex', flexDirection: 'column', flex: 1, padding: '15px' }}>
            <Button onClick={() => {
                dispatch({
                    home: {
                        ...state['home'],
                        accounts: {
                            ...state['home']['accounts'],
                            add: {
                                ...state['home']['accounts']['add'],
                                open: true
                            }
                        }
                    }
                });
            }}>Add</Button>
            <Dialog open={state['home']['accounts']['add']['open']} onClose={() => {
                dispatch({
                    home: {
                        ...state['home'],
                        accounts: {
                            ...state['home']['accounts'],
                            add: {
                                ...state['home']['accounts']['add'],
                                open: false
                            }
                        }
                    }
                });
            }}>
                <DialogTitle>Add Account</DialogTitle>
                <Divider />
                <DialogContent>
                    <Stack spacing={2}>
                        <FormControl>
                            <InputLabel>Institution</InputLabel>
                            <Select onChange={(e) => {
                                dispatch({
                                    home: {
                                        ...state['home'],
                                        accounts: {
                                            ...state['home']['accounts'],
                                            add: {
                                                ...state['home']['accounts']['add'],
                                                institutionID: e.target.value
                                            }
                                        }
                                    }
                                });
                            }} value={state['institutionID']} label='Institution' required>{state['institutions'].map((institution) => (
                                <MenuItem value={institution['id']}>{institution['name']}</MenuItem>
                            ))}</Select>
                        </FormControl>
                        <TextField onChange={(e) => {
                            dispatch({
                                home: {
                                    ...state['home'],
                                    accounts: {
                                        ...state['home']['accounts'],
                                        add: {
                                            ...state['home']['accounts']['add'],
                                            type: e.target.value
                                        }
                                    }
                                }
                            });
                        }} value={state['type']} label='Type' required fullWidth />
                        <TextField onChange={(e) => {
                            dispatch({
                                home: {
                                    ...state['home'],
                                    accounts: {
                                        ...state['home']['accounts'],
                                        add: {
                                            ...state['home']['accounts']['add'],
                                            accountNumber: e.target.value
                                        }
                                    }
                                }
                            });
                        }} value={state['accountNumber']} label='Account #' required fullWidth />
                    </Stack>
                </DialogContent>
                <Divider />
                <DialogActions>
                    <Button onClick={() => {
                        window.eel?.create('account', {
                            institutionID: state['home']['accounts']['add']['institutionID'],
                            type: state['home']['accounts']['add']['type'],
                            accountNumber: state['home']['accounts']['add']['accountNumber']
                        })((accounts) => {
                            dispatch({
                                accounts: accounts,
                                home: {
                                    ...state['home'],
                                    accounts: {
                                        ...state['home']['accounts'],
                                        add: {
                                            open: false,
                                            institutionID: undefined,
                                            type: undefined,
                                            accountNumber: undefined
                                        }
                                    }
                                }
                            });
                        });
                    }}>Add</Button>
                </DialogActions>
            </Dialog>
            <DataGrid rows={state['accounts']} columns={[{
                field: 'id',
                headerName: 'ID'
            }, {
                field: 'institution',
                headerName: 'Institution',
                flex: 1,
                valueGetter: (params) => state['institutions'].find((institution) => institution['id'] === params.row.institution_id)['name']

            }, {
                field: 'type',
                headerName: 'Type',
                flex: 1
            },
            {
                field: 'account_number',
                headerName: 'Account #',
                flex: 1
            },
            {
                field: 'Delete',
                headerName: '',
                sortable: false,
                filterable: false,
                renderCell: (params) => <Button onClick={() => { window.eel?.delete('account', params.id)((accounts) => { dispatch({ accounts: accounts }); }); }} fullWidth>Delete</Button>
            }]} autoHeight />
        </Box>
    );
}