import { Box, Button, TextField, Dialog, DialogTitle, DialogContent, DialogActions, Divider } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';

export default function Institutions({ state, dispatch, hidden }) {
    return (
        <Box sx={{ display: hidden ? 'none' : 'flex', flexDirection: 'column', flex: 1, padding: '15px' }}>
            <Button onClick={() => {
                dispatch({
                    home: {
                        ...state['home'],
                        institutions: {
                            ...state['home']['institutions'],
                            add: {
                                ...state['home']['institutions']['add'],
                                open: true
                            }
                        }
                    }
                });
            }}>Add</Button>
            <Dialog open={state['home']['institutions']['add']['open']} onClose={() => {
                dispatch({
                    home: {
                        ...state['home'],
                        institutions: {
                            ...state['home']['institutions'],
                            add: {
                                ...state['home']['institutions']['add'],
                                open: false
                            }
                        }
                    }
                });
            }}>
                <DialogTitle>Add Institution</DialogTitle>
                <Divider />
                <DialogContent>
                    <TextField onChange={(e) => {
                        dispatch({
                            home: {
                                ...state['home'],
                                institutions: {
                                    ...state['home']['institutions'],
                                    add: {
                                        ...state['home']['institutions']['add'],
                                        name: e.target.value
                                    }
                                }
                            }
                        });
                    }} value={state['home']['institutions']['add']['name']} label='Name' required fullWidth />
                </DialogContent>
                <Divider />
                <DialogActions>
                    <Button onClick={() => {
                        window.eel?.create('institution', { name: state['home']['institutions']['add']['name'] })((institutions) => {
                            dispatch({
                                institutions: institutions,
                                home: {
                                    ...state['home'],
                                    institutions: {
                                        ...state['home']['institutions'],
                                        add: {
                                            open: false,
                                            name: undefined
                                        }
                                    }
                                }
                            });
                        });
                    }}>Add</Button>
                </DialogActions>
            </Dialog>
            <DataGrid rows={state['institutions']} columns={[{
                field: 'id',
                headerName: 'ID'
            }, {
                field: 'name',
                headerName: 'Name',
                flex: 1
            },
            {
                field: 'Delete',
                headerName: '',
                sortable: false,
                filterable: false,
                renderCell: (params) => <Button onClick={() => { window.eel?.delete('institution', params.id)((institutions) => { dispatch({ institutions: institutions }); }); }} fullWidth>Delete</Button>
            }]} autoHeight />
        </Box>
    );
}